.Favorites_img {
  width: 30px;
  height: 30px;
  margin: 0 10px 10px 0;
}

.Favorites_h3 {
  color: white;
  display: inline;
}

.Favorites_h1 {
  color: white;
  padding-left: 10%;
  margin-top: 50px;
}

.Favorites_img:hover {
  animation: shake 150ms 2 linear;
}

@keyframes shake {
  0% {
    transform: translate(3px, 0);
  }
  50% {
    transform: translate(-3px, 0);
  }
  100% {
    transform: translate(0, 0);
  }
}

@media (max-width: 400px) {
  .Favorites_h1 {
    padding: 0;
  }
}
